html, body {
  margin: 0;
  height: 100%;
}

#root {
  height: inherit;
}


.MuiTableRow-root {
    &:hover {
      background-color: #eceff2;
    }
  }

  .MuiTable-root {
    background-color: white;
  }
  
  .MuiTableContainer-root { 
    box-shadow:0 1px 3px 0 rgba(0, 0, 0, 0.10);
  }
.table {
  background: white;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.10)
}
.table td, .table th {
  font-size: 14px;
}