.boxWrap {
  display: flex;
  flex-direction: initial;
}

.boxWrap .box {
  display: flex;
  align-items: center;
  flex: 1;
  height: 300px;
}

.boxWrap .box .explain {
  position: relative;
  width: 100%;
  padding: 5%;
}

@media screen and (max-width: 768px) {
  .boxWrap:nth-child(even) {
    flex-direction: column;
  }
  .boxWrap:nth-child(odd) {
    flex-direction: column-reverse;
  }
  .boxWrap .box {
    flex: auto;
  }
}
